import {
  FormControl,
  FormControlLabel,
  Radio,
  RadioGroup,
} from "@mui/material";
import { useEffect } from "react";
import { Controller, useForm } from "react-hook-form";
export interface props {
  data: any;
  classes: any;
  index: number;
  isEdit: boolean;
  handleUpdateDateTypeFromChild: (index: number, type: string) => void;
}

const RenderDateTypeSetting: React.FC<props> = ({
  classes,
  data,
  index,
  isEdit,
  handleUpdateDateTypeFromChild,
}) => {
  const { control, setValue } = useForm<any>({
    mode: "onChange",
    defaultValues: {
      dateType: String(data?.date_type) || "3",
    },
  });

  const handleUpdateDateType = (index: number, type: string) => {
    handleUpdateDateTypeFromChild(index, type);
  };

  const renderDateType = (type: string) => {
    switch (type) {
      case "3":
        return "";
      case "2":
        return "";
      case "1":
        return "";
      case "0":
        return "";

      default:
        break;
    }
  };

  useEffect(() => {
    setValue("dateType", String(data?.date_type));
  }, [data]);

  return (
    <Controller
      name="dateType"
      control={control}
      render={({ field }) =>
        isEdit ? (
          <FormControl component="fieldset">
            <RadioGroup
              {...field}
              row={true}
              onChange={(e) => {
                e.preventDefault();
                field.onChange(e);
                handleUpdateDateType(index, e.target.value);
              }}
            >
              <FormControlLabel
                value="0"
                control={<Radio color="primary" />}
                label=""
              />
              <FormControlLabel
                value="1"
                control={<Radio color="primary" />}
                label=""
              />
              <FormControlLabel
                value="2"
                control={<Radio color="primary" />}
                label=""
              />
              <FormControlLabel
                value="3"
                control={<Radio color="primary" />}
                label=""
              />
            </RadioGroup>
          </FormControl>
        ) : (
          <p style={{ fontSize: "1rem" }}>
            {renderDateType(String(data?.date_type))}
          </p>
        )
      }
    />
  );
};

export default RenderDateTypeSetting;
